import React from 'react';
import { Pages } from "./Pages";
import Me from "../component/Me";
import About from '../component/About';
import Blog from '../component/Blog';

export const Navigator = (route) => {
    switch (route) {
        case Pages.Main:
            return <Me/>    
        case Pages.About:
            return <About/>
        case Pages.Blog:
            return <Blog />
        default:
            return <Me/>
    }
}