import React, { Component } from 'react'
import { Text, View, FlatList } from 'react-native'
import { connect } from 'react-redux';
import { GetReadingList } from '../redux/actions';

class Blog extends Component {
    componentDidMount() {
        this.props.GetReadingList()
    }

    render() {
        console.log("READING_LIST == ", this.props.READING_LIST)
        return (
            <View style={{ flex: 1, alignItems: 'center' }}>
                <FlatList
                 data={this.props.READING_LIST}
                 horizontal={false}
                 style={{
                     width: '100%',
                 }}
                 contentContainerStyle={{ alignItems: 'center' }}
                    renderItem={({ item, index }) => <Text>{item.title}</Text>}
                 keyExtractor={item => item.link_id}
                 decelerationRate='normal'
                 alwaysBounceHorizontal={false}
                 showsVerticalScrollIndicator={false}
                />
            </View>
        )
    }
}
const mapStateToProps = state => {
    return {
        READING_LIST: state.Refind.READING_LIST
    };
};

export default connect(
    mapStateToProps,
    {
        GetReadingList
    },
)(Blog);